header {
  position: relative;
}

#headerImage {
  width: 100%;
  height: auto;
  z-index: -100;
  display: block;
  background-position: top center;
  background-size: 100% auto;
  background-repeat: no-repeat;
}

.welcome-bold {
  font-weight: 700;
}

.welcome-blue {
  color: #8eadB1;
}

header .chevron.circle.down.icon {
  position: absolute;
  font-size: 5rem;
  color: #8eadB1;
  cursor: pointer;
  top: 80%;
  left: 50%;
  transition: all 0.3s linear;
  transform: translateX(-50%);
}

header .chevron.circle.down.icon:hover {
  color: #e4cd8a;
  font-size: 6rem;
  left: 50%;
  transform: translateX(-50%);
}

header .welcome-header-heading {
  font-family: "Amatic SC", cursive;
  font-size: 10rem;
  position: absolute;
  color: #e4cd8a;
  top: 200px;
  left: 10%;
}

header .welcome-header-paragraph {
  font-family: "Amatic SC", cursive;
  font-size: 4.2rem;
  color: #e4cd8a;
  position: absolute;
  line-height: 4.6rem;
  margin-top: 20px;
}

header p.welcome-header-paragraph:nth-child(3) {
  top: 350px;
  left: 10%;
}

header p.welcome-header-paragraph:nth-child(4) {
  top: 550px;
  left: 10%;
}

.welcome-newspaper {
  font-family: 'Droid Serif', serif;
  font-size: 1.4rem;
  padding-top: 60px;
  min-height: 1000px;
  text-align: center;
  background-color: #e4ddce;
  color: #000000;
  position: relative;
}

.welcome-newspaper .chevron.circle.down.icon {
  position: absolute;
  font-size: 5rem;
  color: #000000;
  cursor: pointer;
  top: 90%;
  left: 50%;
  transition: all 0.3s linear;
  transform: translateX(-50%);
}

.welcome-newspaper .chevron.circle.down.icon:hover {
  color: #ae996b;
  font-size: 6rem;
  left: 50%;
  transform: translateX(-50%);
}

.welcome-newspaper .welcome-header-heading {
  font-family: "Amatic SC", cursive;
  font-size: 5rem;
  color: #e4cd8a;
  text-align: center;
}

.welcome-newspaper p {
  margin-top: 0;
  margin-bottom: 20px;
  text-align: left;
}

.welcome-newspaper .newspaper-huge-heading {
  font-family: "Playfair Display", serif;
  font-weight: 700;
  font-size: 80px;
  text-transform: uppercase;
  display: inline-block;
  line-height: 72px;
  margin-bottom: 20px;
}

.welcome-newspaper .newspaper-subhead {
  text-transform: uppercase;
  border-bottom: 2px solid #2f2f2f;
  border-top: 2px solid #2f2f2f;
  padding: 12px 0 12px 0;
}

.welcome-newspaper .collumns {
  font-size: 0;
  line-height: 0;
  word-spacing: -.31em;
  display: inline-block;
  margin: 30px 2% 0 2%;
}

.welcome-newspaper .collumn {
  font-size: 1.6rem;
  line-height: 20px;
  width: 17.5%;
  display: inline-block;
  padding: 0 1% 0 1%;
  vertical-align: top;
  margin-bottom: 3rem;
  transition: all 0.3s linear;
}

.welcome-newspaper .collumn + .collumn {
  border-left: 1px solid #2f2f2f;
}

.welcome-newspaper .collumn > img {
  width: 60%;
}

.welcome-newspaper .head {
  position: relative;
  text-align: center;
}

.welcome-newspaper .collumn .headline {
  text-align: center;
  line-height: normal;
  font-family: "Playfair Display", serif;
  display: block;
  margin: 0 auto;
}

.welcome-newspaper .collumn .headline.column1, .welcome-newspaper .collumn .headline.column4 {
  font-weight: 400;
  font-style: italic;
  font-size: 3.6rem;
  padding: 10px 0 10px 0;
}

.welcome-newspaper .collumn .headline.subcolumn1 {
  font-weight: bold;
  font-size: 1.2rem;
  padding: 10px 0 10px 0;
}

.welcome-newspaper .collumn .headline.subcolumn5 {
  font-weight: bold;
  font-size: 1.5rem;
  padding: 10px 0 10px 0;
}

.welcome-newspaper .collumn .headline.subcolumn1:before, .welcome-newspaper .collumn .headline.subcolumn5:before {
  border-top: 1px solid #2f2f2f;
  content: '';
  width: 100px;
  height: 7px;
  display: block;
  margin: 0 auto;
}

.welcome-newspaper .collumn .headline.subcolumn1:after, .welcome-newspaper .collumn .headline.subcolumn5:after {
  border-bottom: 1px solid #2f2f2f;
  content: '';
  width: 100px;
  height: 10px;
  display: block;
  margin: 0 auto;
}

.welcome-newspaper .collumn .headline.column2 {
  font-weight: 400;
  font-size: 4.2rem;
  text-transform: uppercase;
  font-style: italic;
  box-sizing: border-box;
  padding: 10px 0 10px 0;
}

.welcome-newspaper .collumn .headline.subcolumn2, .welcome-newspaper .collumn .headline.subcolumn4 {
  font-weight: 400;
  font-size: 1.8rem;
  box-sizing: border-box;
  padding: 10px 0 10px 0;
}

.welcome-newspaper .collumn .headline.subcolumn2:before, .welcome-newspaper .collumn .headline.subcolumn4:before {
  border-top: 1px solid #2f2f2f;
  content: '';
  width: 100px;
  height: 7px;
  display: block;
  margin: 0 auto;
}

.welcome-newspaper .collumn .headline.subcolumn2:after, .welcome-newspaper .collumn .headline.subcolumn4:after {
  border-bottom: 1px solid #2f2f2f;
  content: '';
  width: 100px;
  height: 10px;
  display: block;
  margin: 0 auto;
}

.welcome-newspaper .collumn .headline.column3, .welcome-newspaper .collumn .headline.column5 {
  font-weight: 700;
  font-size: 3rem;
  text-transform: uppercase;
  padding: 10px 0 10px 0;
}

.welcome-newspaper .collumn .headline.subcolumn3 {
  font-weight: 400;
  font-style: italic;
  font-size: 24px;
  box-sizing: border-box;
  padding: 10px 0 10px 0;
}

.welcome-newspaper .collumn .headline.subcolumn3:before {
  border-top: 1px solid #2f2f2f;
  content: '';
  width: 100px;
  height: 7px;
  display: block;
  margin: 0 auto;
}

.welcome-newspaper .collumn .headline.subcolumn3:after {
  border-bottom: 1px solid #2f2f2f;
  content: '';
  width: 100px;
  height: 13px;
  display: block;
  margin: 0 auto;
}

@media only all and (max-width: 1200px) {
  .welcome-newspaper .collumn {
    width: 31%;
  }
}

@media only all and (max-width: 1050px) {
  .welcome-newspaper .collumn {
    width: 47%;
  }
  .welcome-newspaper .collumn:last-of-type {
    width: 100%;
    margin: 2px;
    padding-bottom: 60px;
  }
  .welcome-newspaper .chevron.circle.down.icon {
    top: 97%;
  }
}

@media only all and (max-width: 600px) {
  .welcome-newspaper .chevron.circle.down.icon {
    top: 98%;
  }
  .welcome-newspaper .collumn {
    width: 100%;
    margin: 2px;
  }
  .welcome-newspaper .collumn + .collumn {
    border-left: none;
    border-bottom: 1px solid #2f2f2f;
  }
  .welcome-newspaper .collumn:last-of-type {
    width: 100%;
    margin: 2px;
    padding-bottom: 60px;
  }
  .welcome-newspaper .newspaper-huge-heading {
    max-width: 320px;
    font-size: 60px;
    line-height: 54px;
    overflow: hidden;
  }
}

.welcome-charts {
  padding-top: 60px;
  height: 1000px;
  background-color: #2c3338;
  color: #dcddde;
}

.welcome-charts .charts-huge-heading {
  font-family: "Droid Serif", serif;
  font-size: 3.6rem;
}
